.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 80px;
 }
 .form_container {
     height: 300px;
     width: 520px;
 }
 .form_list {
     display: flex;
     flex-direction: column;
     margin: 5px;
 }
 .form_label {
     display: flex;
     flex-direction: column;
     margin-top: 10px;
 }
 .input_field {
     height: 28px;
     width: 500px;
     margin-top: 5px;
     outline: none;
     border: 1px solid;
     border-radius: 5px;
     border-color: #5181a1;
 }
 .submit_btn {
     height: 30px;
     width: 100px;
     margin-top: 10px;
     border: 2px solid;
     border-radius: 5px;
     border-color: #5181a1;
     color: #5181a1;
     background: transparent;
 }
 .submit_btn:hover {
     background: #5181a1;
     color: #fff;
 }